import React from 'react';
import { Link } from 'react-router-dom';
import { useTranslation } from 'react-i18next';
import './styles.scss';

// Bunun yerine bir base64 kodlu küçük resim veya URL kullanın
const placeholderImage = 'data:image/svg+xml;base64,';

interface ListingCardProps {
  id: string;
  title: string;
  slug: string;
  price: number;
  currency: string;
  location: string;
  condition?: string;
  featuredImage?: string;
  images?: string[];
  createdAt: string;
  isPromoted?: boolean;
  isFeatured?: boolean;
  categoryName?: string;
  categorySlug?: string;
  userName?: string;
  userImage?: string;
  className?: string;
}

const ListingCard: React.FC<ListingCardProps> = ({
  id,
  title,
  slug,
  price,
  currency,
  location,
  condition,
  featuredImage,
  images,
  createdAt,
  isPromoted,
  isFeatured,
  categoryName,
  categorySlug,
  userName,
  userImage,
  className
}) => {
  const { t } = useTranslation();
  
  // Format the price with thousand separators
  const formatPrice = (value: number): string => {
    return value.toLocaleString('az-AZ');
  };
  
  // Format date to relative time (e.g., "2 days ago")
  const formatDate = (dateString: string): string => {
    const now = new Date();
    const date = new Date(dateString);
    const diffTime = Math.abs(now.getTime() - date.getTime());
    const diffDays = Math.floor(diffTime / (1000 * 60 * 60 * 24));
    
    if (diffDays === 0) {
      return t('date.today');
    } else if (diffDays === 1) {
      return t('date.yesterday');
    } else if (diffDays < 7) {
      return t('date.daysAgo', { count: diffDays });
    } else if (diffDays < 30) {
      const weeks = Math.floor(diffDays / 7);
      return t('date.weeksAgo', { count: weeks });
    } else {
      const months = Math.floor(diffDays / 30);
      return t('date.monthsAgo', { count: months });
    }
  };
  
  // Get image to display
  const displayImage = featuredImage || (images && images.length > 0 ? images[0] : placeholderImage);
  
  const handleImageError = (e: React.SyntheticEvent<HTMLImageElement>) => {
    e.currentTarget.src = placeholderImage;
  };
  
  return (
    <div className={`listing-card ${className || ''} ${isPromoted ? 'promoted' : ''} ${isFeatured ? 'featured' : ''}`}>
      <Link to={`/listings/${slug}`} className="listing-card__link">
        <div className="listing-card__image-container">
          <img 
            src={displayImage} 
            alt={title} 
            className="listing-card__image" 
            onError={handleImageError}
          />
          {isPromoted && (
            <span className="listing-card__badge listing-card__badge--promoted">
              {t('listing.promoted')}
            </span>
          )}
          {isFeatured && (
            <span className="listing-card__badge listing-card__badge--featured">
              {t('listing.featured')}
            </span>
          )}
        </div>
        
        <div className="listing-card__content">
          <h3 className="listing-card__title">{title}</h3>
          
          <div className="listing-card__price">
            {formatPrice(price)} {currency}
          </div>
          
          <div className="listing-card__meta">
            <div className="listing-card__location">
              <i className="icon-location"></i> {location}
            </div>
            
            <div className="listing-card__date">
              {formatDate(createdAt)}
            </div>
          </div>
          
          {(categoryName && categorySlug) && (
            <Link to={`/categories/${categorySlug}`} className="listing-card__category">
              {categoryName}
            </Link>
          )}
          
          {condition && (
            <div className="listing-card__condition">
              {t(`condition.${condition}`)}
            </div>
          )}
        </div>
      </Link>
      
      {(userName && userImage) && (
        <div className="listing-card__user">
          <img 
            src={userImage} 
            alt={userName} 
            className="listing-card__user-image"
            onError={handleImageError}
          />
          <span className="listing-card__user-name">{userName}</span>
        </div>
      )}
    </div>
  );
};

export default ListingCard; 